import React, { useState } from 'react';
import "./VistaUsuario.css";

const VistaUsuario = () => {
  const [users, setUsers] = useState([
    {
        id: 1,
        NombreUsuario:"JLzo" ,
        nombre: "Jose Lazo",
        Email: "JoseL.1@gmail.com",
        Telefono: "996923731",
        TipoPersona: "Dueño",
        validado: false,

      },
      {
        id: 2,
        NombreUsuario:"WLas" ,
        nombre: " willi Las",
        Email: "Will.1@gmail.com",
        Telefono: "952537822",
        TipoPersona: "Dueño",
        validado: true,
      },
      {
        id: 3,
        NombreUsuario:"JJer" ,
        nombre: "Juan jimenes",
        Email: "JuJi.1@gmail.com",
        Telefono: "979129737",
        TipoPersona: "Dueño",
        validado: true,
      },
  ]);

    // Función para validar un negocio
    const validarUsuario = (id) => {
        const nuevosUsuario = users.map((usuario) =>
          usuario.id === id ? { ...usuario, validado: true } : usuario
        );
        setUsers(nuevosUsuario);
      };

  return (
    <div className="user-table-container">
      <h2>Lista de Usuarios</h2>
      <table className="user-table">
        <thead>
          <tr>
            <th>ID</th>
            <th>usuario</th>
            <th>Nombre</th>
            <th>Email</th>
            <th>Telefono</th>
            <th>Tipo</th>
            <th>validado</th>
          </tr>
        </thead>
        <tbody>
          {users.map((user) => (
            <tr key={user.id}>
              <td>{user.id}</td>
              <td>{user.NombreUsuario}</td>
              <td>{user.nombre}</td>
              <td>{user.Email}</td>
              <td>{user.Telefono}</td>
              <td>{user.TipoPersona}</td>
              <td>{user.validado}</td>
              <td>
                {user.validado ? (
                  <span className="estado-validado">Validado</span>
                ) : (
                  <span className="estado-pendiente">Pendiente</span>
                )}
              </td>
              <td>
                {!user.validado && (
                  <button
                    className="btn-validar"
                    onClick={() => validarUsuario(user.id)}
                  >
                    Validar
                  </button>
                )}
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default VistaUsuario;
